
.projects-section {
  background: rgb(0, 0, 0);
  min-height: 100vh;
  transition: .4s all ease-in-out;
}

div.projects-container { 
  height: 90%;
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  /*background: red;*/
  margin: auto;
  padding: 2.5rem 0;
}

.projects-container h2 {
  font-weight: 400;
  letter-spacing: 5px;
  font-size: 2rem;
}

div.tag-buttons {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  width: 35%;
  height: 5%;
  margin: 30px auto 40px;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid white;
  z-index: 1;
}
.tag {
  outline: none;
	border: none;
	color: white;
	margin: 5px 10px;
	background: transparent;
  width: 20%;
	cursor: pointer;
  font-size: 20px;
  letter-spacing: 1px;
}
.tag:hover {
  color: #00A8E8;
  transition: .2s all ease-in-out;
}

.active-project {
  color: #00A8E8;
  transition: .2s all ease-in-out;
}

