.container-list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.project-card {
  z-index: 1;
  width: 20rem;
  height: 18.3rem;
  display: flex;
  flex-direction: column;
  background-color: rgb(30, 30, 50);
  margin: 1rem;
  /*filter: drop-shadow(0px 0px 3px rgba(0, 166, 232, 0.392));
  filter: drop-shadow(0px 0px 3px rgba(255, 255, 255, 0.203));*/
  border-bottom-right-radius: 25px;
  border-top-left-radius: 25px;
  transition: .4s all ease-in-out;
}
.project-card:hover {
  z-index: 2;
  filter: drop-shadow(0px 0px 2px rgba(255, 255, 255, 1));
  transform: scale(1.1);
  transition: .4s all ease-in-out;
}
div.img-desc-container {
  height: 61.5%;
  background-color: rgb(30, 30, 50);
  border-top-left-radius: 25px;
  cursor: pointer;
}
div.img-desc-container .desc-container {
  border-top-left-radius: 25px;
  cursor: pointer;
  position: absolute;
  width: 20rem;
}
.project-desc {
  /*display: none;
  z-index: 3;
  position: absolute;*/
  opacity: 100;
  font-size: 0.8rem;
  text-align: center;
  padding: 0 0.8rem;
  padding-top: 0.4rem;
  margin: 4.2rem 0;
  opacity: 0;
  transition: 0.4s all ease-in-out;
}
div.tit-btn-container {
  height: 38.5%;
  background: rgb(30, 30, 50);
  border-bottom-right-radius: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
div.tit-btn-container h2 {
  font-weight: 400;
  font-size: 1.25rem;
  text-transform: uppercase;
  letter-spacing: 2px;
}
div.tit-btn-container a {
  text-decoration: none;
  border: 2px solid #00A8E8;
  border-radius: 8px;
  width: 5.5rem;
  color: #00A8E8;
  text-transform: uppercase;
  font-size: 1rem;
  margin-top: 1.2rem;
  transition: 0.4s all ease-in-out;
}
div.tit-btn-container a:hover {
  background-color: #00A8E8;
  color: rgb(30, 30, 50);
  letter-spacing: 1px;
  width: 5.8rem;
  transition: 0.4s all ease-in-out;
}
.project-image {
  width: 20rem; 
  border-top-left-radius: 25px;
  transition: 0.4s all ease-in-out;
}
div.img-desc-container:hover .project-image{
  transition: 0.4s all ease-in-out;
  opacity: 0;
}
div.img-desc-container:hover .project-desc {
  opacity: 100%;
  transition: 0.4s all ease-in-out;
}

@media only screen and (max-width: 1400px) {
  .project-card {
    width: 16rem;
    height: 14.3rem;
    margin: 1rem;
  }
  div.img-desc-container .desc-container {
    border-top-left-radius: 25px;
    cursor: pointer;
    position: absolute;
    width: 16rem;
  }
  .project-desc {
    margin: 3rem 0;
  }
  div.tit-btn-container h2 {
    font-size: 1rem;
  }
  div.tit-btn-container a {
    margin-top: 0.35rem;
  }
  .project-image {
    width: 16rem; 
  }

  
  div.tag-buttons {
    width: 75%;
  }
  .tag {
    font-size: 80%;
  }
}