/* MENU */
.menu {
    /*background-color: rgba(30, 30, 50, 0.338);*/
    display: flex;
    flex-direction: column;
    align-items: center;
    bottom: 30%;
    left: 25px;
    border-radius: 15px;
    padding: 30px 0px;
    width: 4rem;
    border: 1px solid;
    z-index: 998;
    position: fixed;
    transition: .4s all ease-in-out;
  }
  .menu:hover {
    border: 1px solid rgb(30, 30, 50);
    background-color: rgb(30, 30, 50);
    transition: .4s all ease-in-out;
  }
  
  /*
  .menu-options {
    margin: 15px 0px;
    cursor: pointer;
  }
  */
  .menu-options {
    margin: 20px 0px;
    cursor: pointer;
  }
  .menu-options:nth-child(6) {
    display: none;
  }
  

  a.menu-link {
    visibility: visible;
    text-decoration: none;
    color: #fff;
    transition: .4s all ease-in-out;
  }
  a.menu-link:hover {
    color: #00A8E8;
    /*background-color: rgba(15, 15, 22, 0.23);
    box-shadow: inset 1px 1px 2px rgba(5, 5, 8, 0.765);*/
    padding: 15px;
    border-radius: 50%;
  }
  a.active {
    color: #ffffff;
  }
  .span-menu {
    display: inline-block;
    font-size: 0;
    transition: .0s ;
  }
  .menu-options:hover .span-menu {
    background-color: rgb(30, 30, 50);
    /*background-color: rgb(30, 30, 50);
    border: #000 6.5px solid;*/
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 43px;
    transform: translateY(-70%);
    border-radius: 15px;
    width: 6.1rem;
    height: 3.5rem;
    padding: 10px 0;
    z-index: 999;
    font-size: 15px;  
    transition: .4s all;
  }
  span.span-menu.span-config:hover {
    width: 7.1rem;
    height: 8.5rem;
    border-radius: 9px;
    left: 52px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    font-size: 13px;
  }
  .span-lang {
    display: none;
  }
  span.span-menu.span-config:hover .span-lang{
    display: flex;
    flex-direction: column;
  }
  .lang-title {
    font-size: 10px;
  }
  div.langs a{
    text-decoration: none;
    color: white;
    display: flex;
    flex-direction: column;
    padding: 5px;
    transition: .08s all ease-in-out;
  }
  
  @media only screen and (max-width: 820px) {
    .menu {
      display: none;
    }
  }