/* SKILLS */

.skills-section {
  margin: auto;
  padding: 0 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background: rgb(0,0,0);
}

.skills-container {
  width: 100%;
  height: 80%;
  /*background: green;*/
  z-index: 2;
}
.skills-container .skills-title h2 {
  font-size: 2.3rem;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 5px;
  padding-bottom: 30px;
}
.skills-subcontainer {
  margin: auto;
  width: 85%;
  height: 85%;
  /*background: red;*/
}
.skills-web {
  margin: auto;
  width: 100%;
  height: 40%;
  /*background: rgb(0, 255, 195);*/
  display: flex;
}
.front-container {
  /*background: gray;*/
  width: 50%;
}
.back-container {
  width: 50%;
}


.front-title {
  height: 35%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding-top: 1rem;
}
div.front-title h3,
div.back-title h3,
div.variado-title h3 {
  font-weight: 400;
  letter-spacing: 2px;
}
.front-list {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  height: 46%;
  width: 90%;
  margin: auto;
  padding-top: 0.8rem;
}

.front-list div.front-item:nth-child(1) img {
  width: 58%;
  filter: invert(98%) sepia(100%) saturate(0%) hue-rotate(291deg) brightness(107%) contrast(101%);
  filter: invert(76) sepia(207%) saturate(780%) hue-rotate(168deg) brightness(95%) contrast(91%);
  margin: 0 auto;
  cursor:pointer;
  transition: 0.2s all ease-in-out;
}
