.variado-list div.variado-item span {
  margin: 0 3.8rem;
}
.front-list div.front-item img,
.back-list div.back-item img,
.variado-list div.variado-item img {
  opacity: 70%;
  transition: 0.2s all ease-in-out;
}
.front-list div.front-item img:hover,
.back-list div.back-item img:hover,
.variado-list div.variado-item img:hover {
  filter: none;
  /*transform: scale(1.3);*/
  transform: translateY(-30%);
  transition: 0.2s all ease-in-out;
  opacity: 100%;
}



.front-item span,
.back-item span,
.variado-item span {
  display: flex;
  flex-direction: column;
  opacity: 0;
  /*background: white;
  color: black;*/
  color: #fff;
  border-radius: 10px;
  transition: 0.2s all ease-in-out;
}
.front-item:hover span,
.back-item:hover span,
.variado-item:hover span {
  opacity: 100;
  transition: 0.2s all ease-in-out;
}



.front-list div.front-item:nth-child(4) img:hover {
  filter: invert(22%) sepia(55%) saturate(602%) hue-rotate(223deg) brightness(100%) contrast(92%);
  transition: 0.2s all ease-in-out;
  transform: translateY(-30%);
  opacity: 100%;
}
