span.sphere {
  z-index: 0;
  display: flex;
  position: absolute;
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
}
span.sphere .tagcloud {
  display: inline-block;
  font-size: 20px;
  opacity: 10%;
  color: #fff;
}
span.sphere .tagcloud:nth-child(2) {
  display: none;
}



@media only screen and (max-width: 1200px) {
  
  .skills-container {
    height: 90%;
  }
  .skills-container .skills-title h2 {
    font-size: 1.5rem;
  }
  .skills-subcontainer {
    width: 95%;
  }
  .back-container {
    width: 50%;
  }
  
  
  .front-title {
    padding-top: 0rem;
  }

  .front-list {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: 33% 33% 33%;
    height: 60%;
    width: 85%;
  }
  .front-list div.front-item:nth-child(1) img {
    width: 60%;
  }
  .front-list div.front-item:nth-child(2) img {
    width: 60%;
    padding-top: 0rem;
  }
  .front-list div.front-item:nth-child(3) img{
    width: 50%;
    padding-top: 0.1rem;
  }
  .front-list div.front-item:nth-child(4) img{
    width: 60%;
    padding-top: 0.1rem;
  }
  .front-list div.front-item:nth-child(5) img{
    width: 60%;
    padding-top: 0.2rem;
  }
  .front-list div.front-item:nth-child(6) img{
    width: 55%;
    padding-top: 0.3rem;
  }

  .back-title {
    padding-top: 0rem;
  }
  .back-list {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: 33% 33% 33%;
    height: 60%;
    width: 85%;
  }

  .variado-list {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-template-columns: 33% 33% 33%;
    height: 60%;
    width: 100%;
  }

  .variado-list div.variado-item span {
    margin: 0 0.8rem;
  }

  .front-item span,
  .back-item span,
  .variado-item span {
    font-size: 10px;
  }
  .front-item:hover span,
  .back-item:hover span,
  .variado-item:hover span {
    opacity: 100;
    transition: 0.2s all ease-in-out;
    background: none;
    color: white;
    border-radius: 10px;
  }

  span.sphere {
    display: none;
  }
}