/* ABOUT ME */

.about-me {
  margin: auto;
  padding: 0 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background: rgb(0,0,0);
  background: linear-gradient(0deg, rgba(0,0,0,1) 70%, rgba(18, 18, 18, 0) 98%);
}
.about-me div {
  /*background-color: rgb(30, 30, 50);*/
  width: 85%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  border-radius: 20px;
  padding: 20px 10px;
}

div.about-division {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  height: 65%;
  border: 1px solid white;
  transition: 0.5s all ease-in-out;
}
div.about-division:hover {
  background-color: rgb(30, 30, 50);
  border: 1px solid rgb(30, 30, 50);
  transition: 0.5s all ease-in-out;
}

div.img-about {
  width: 30%;
  display: flex;
  align-items: center;
  margin: auto;
}
.about-division .circle-about {
  position: relative;
  width: 400px;
  height: 400px;
  background: #000;
  border-radius: 50%;
  overflow: hidden;
  transform: scale(80%);
  transition: 0.5s all ease-in-out;
}
div.about-division:hover .circle-about {
  background: rgb(30, 30, 50);
  transition: 0.5s all ease-in-out;
}
.about-division .circle-about::before {
  content: '';
  position: absolute;
  inset: -10px 140px;
  /*background: linear-gradient(315deg, #00A8E8, #d400d4);*/
  background: #00A8E8;
  /*background: linear-gradient(315deg, #00A8E8, #ffffff);*/
  transition: 0.5s;
  animation: photo-about 4s linear infinite;
  z-index: 1;
}
.about-division .circle-about:hover::before {
  inset: -20px 0px;
  z-index: 1;
  transition: 0.25s all ease-in-out;
}
@keyframes photo-about {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.about-division .circle-about::after{
  content: '';
  position: absolute;
  inset: 7px;
  background: rgb(0, 0, 0);
  border-radius: 50%;
  z-index: 1;
  transition: 0.5s all ease-in-out;
}
div.about-division:hover .circle-about::after{
  content: '';
  position: absolute;
  inset: 7px;
  background: rgb(30, 30, 50);
  border-radius: 50%;
  z-index: 1;
  transition: 0.5s all ease-in-out;
}
.circle-about .photo-about{
  position: absolute;
  inset: 30px;
  border: 5px solid #fff;
  border-radius: 50%;
  overflow: hidden;
  z-index: 3;
  transform: scale(103%);
  transition: 0.5s all ease-in-out;
}
.circle-about .photo-about:hover {
  border: none;
  transform: scale(114%);
  transition: 0.25s all ease-in-out;
}
.circle-about .photo-about:hover span.shadow-about {
  content: '';
  position: absolute;
  inset: -10px;
  overflow: hidden;
  border-radius: 50%;
  background: rgb(0, 0, 0);
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.775) 0%, rgba(0,0,0,0) 80%);  
  z-index: 3;
  transition: 0.5s all ease-in-out;
}
.circle-about .photo-about img {
  position: absolute;  
  object-fit: cover;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: 0.1s;
}



div.about-info {
  width: 70%;
  display: flex;
}
div.about-info .title-about {
  font-size: 2rem;
  font-weight: 600;
  letter-spacing: 2px;
  display: flex;
  align-items: center;
  margin: auto;
}
.about-data h2.info2-about {
  display: flex;
  font-weight: 600;
  font-size: 1.1rem;
}
h2.info2-about p{
  display: flex;
  align-items: center;
  padding: 0 0 0 5px;
  font-weight: 400;
  font-size: 0.9rem;
}
div.about-data {
  width: 100%;
  padding: 0px 10px 10px;
}
.about-data h3 {
  color: #00A8E8;
  font-size: 1.2rem;
}
.about-data h2 {
  text-align: left;
  letter-spacing: 1.3px;
  font-size: 1.5rem;
}
.about-data p {
  text-align: left;
  letter-spacing: 1px;
  font-size: 1rem;
  padding: 10px 0;
  color: rgb(159, 159, 159);
}

.curriculum a {
  position: relative;
  background: #fff;
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-weight: 400;
  padding: 10px 30px;
  transition: 0.5s;
  border-radius: 8px;
}
.curriculum a:hover {
  background: #00A8E8;
  color: #00A8E8;
}
.curriculum a::before {
  content: '';
  position: absolute;
  inset: 3px;
  background: #000;
  border-radius: 5px;
  transition: 0.5s all ease-in-out;
}
div.about-division:hover .curriculum a::before,
div.about-division:hover .curriculum a i::before,
div.about-division:hover .curriculum a i::after {
  background: rgb(30, 30, 50);
  transition: 0.5s all ease-in-out;
}
.curriculum a span {
  position: relative;
  z-index: 1;
}
.curriculum a i {
  position: absolute;
  inset: 0;
  display: block;
}
.curriculum a i::before {
  content: '';
  position: absolute;
  top: 0;
  left: 80%;
  width: 10px;
  height: 4px;
  background: #000;
  transform: translateX(-50%) skewX(325deg);
  transition: 0.5s;
  transition: 0.5s all ease-in-out;
}
.curriculum a:hover i::before {
  width: 20px;
  left: 20%;
}
.curriculum a i::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 20%;
  width: 10px;
  height: 4px;
  background: #000;
  transform: translateX(-50%) skewX(325deg);
  transition: 0.5s;
  transition: 0.5s all ease-in-out;
}
.curriculum a:hover i::after {
  width: 20px;
  left: 80%;
}


@media only screen and (max-width: 1400px) {
  div.about-division {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 90%;
    width: 80%;
    overflow-y: scroll;
    padding: 10px 0;
  }
  div.img-about {
    display: none;
  } 
  
  
  div.about-info {
    width: 100%;
    height: 100%;
    padding: 0 10px 10px;
  }
  div.about-info .title-about {
    font-size: 1.5rem;
    height: 10%;
    margin: 0 auto;
    padding-top: 7rem;
  }
  .about-data h2.info2-about {
    margin: 0 auto;
    font-size: 1rem;
  }
  div.info2-about {
    width: 100%;
  }
  h2.info2-about {
    width: 100%;
    flex-wrap: wrap;
  }
  h2.info2-about p{
    text-align: left;
    font-size: 0.7rem;
  }
  .about-data h3 {
    font-size: 1rem;
    margin: 0 auto;
    padding-top: 1.2rem;
  }
  .about-data h2 {
    text-align: center;
    font-size: 1.2rem;
  }
  .about-data p {
    text-align: center;
    font-size: 0.7rem;
  }
  
  div.curriculum {
    width: 100%;
    transform: scale(90%);
    padding-top: 0;
  }
}