/* Default CSS */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html{
  scroll-behavior: smooth;
  scroll-padding-top: 75px;
}
body {
  font-weight: 400;
  overflow-x: hidden;
  position: relative;
  background: rgb(0,0,0);
  background: linear-gradient(345deg, rgba(0,0,0,1) 12%, rgba(18,18,18,1) 100%);
  /*background: rgb(0,0,0);
  background: linear-gradient(146deg, rgba(0,0,0,1) 0%, rgba(19,19,19,1) 100%);*/
  color: #fff;
  font-family: 'Poppins';
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  line-height: normal;
}

h1::selection,
h2::selection,
h3::selection,
a::selection,
p::selection {
  background-color: #fff;
  color: #000;
}

/* FOOTER */
.footer {
  border-top: 2px solid white;
  width: 100%;
  align-self: flex-end;
  height: 10vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.footer h3 {
  font-weight: 400;
  font-size: 0.7rem;
  letter-spacing: 4px;
  opacity: 70%;
  margin-top: 0.4rem;
}
.footer p {
  font-size: 0.8rem;
}


/* SCROLL BAR */
/* width */
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  /*box-shadow: inset 0 0 5px grey;
  border-radius: 10px;*/ 
  background: black;
}

 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #ffffff; 
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #00A8E8; 
}



.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


