.contact-section {
  background-color: black;
  width: 100%;
  height: 40vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.contact-title {
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-size: 2rem;
  margin: 1rem 0;
}

.sm-container {
  width: 30%;
  height: 30%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
