.sm {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  overflow: hidden;
  z-index: 2;
}
.btn-sm {
  color: white;
  border: 2px solid white;
  width: 4.5rem;
  height: 4.5rem;
  border-radius: 10px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: .2s all ease-in-out;
  transition-delay: 0.4s;
  z-index: 2;
}
.sm:hover .btn-sm{
  color: #000;
  transition: .2s all ease-in-out;
}

.sm:hover {
  transform: scale(1.05);
  box-shadow: 0px 0px 5px rgba(255, 255, 255, 0.5);
  transition: .2s all ease-in-out;
}

.sm:before {
  content: '';
  position: absolute;
  width: 170%;
  height: 170%;
  background: white;
  transform: rotate(45deg);
  left: -190%;
  top: 100%;
  animation: btn-sm-out 0.7s 1;
}
.sm:hover:before {
  animation: btn-sm 0.7s 1;
  top: -30%;
  left: -20%;
}

@keyframes btn-sm {
  0% {
    left: -130%;
    top: 100%;
  }50% {
    left: 0%;
    top: -60%;
  }100% {
    left: -30%;
    top: -20%;
  }
}
@keyframes btn-sm-out {
  0% {
    left: -30%;
    top: -20%;
  }50% {
    left: 0%;
    top: -60%;
  }100% {
    left: -130%;
    top: 100%;
  }
}

@media only screen and (max-width: 1400px) {
  .sm-container {
    width: 90%;
  }
  .btn-sm {
    width: 3.5rem;
    height: 3.5rem;
  }
}